@use 'base/variables' as v;
@use 'base/mixins' as m;

@include m.tablet {
    .iconos-nosotros {
        margin-top: 4rem;

        @include m.grid(3, 2rem);
    }
}

.icono {
    text-align: center;

    img {
        height: 10rem;
    }

    h3 {
        text-transform: uppercase;
    }
}